body {
  font-family: sans-serif;
  text-align: center;
  background-color: blueviolet;
}

.d-flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  background-color: aqua;
  margin: 5px;
  border-radius: 10px;
  padding: 5px;
  text-align: center;
  width: 200px;
}

.card img {
  width: 100%;
}

.search-box-wrap {
  width: 100%;
  display: block;
  text-align: center;
}

.search-box {
  padding: 5px;
  width: 50%;
  text-align: center;
}

.text-center {
  text-align: center;
}

.mb-1 {
  margin-bottom: 1rem;
}
.mb-2 {
  margin-bottom: 2rem;
}
